.roboto-flex-regular
	font-family: "Roboto Flex", sans-serif
	font-optical-sizing: auto
	font-weight: 400
	font-style: normal
	font-variation-settings: "slnt" 0, "wdth" 100, "GRAD" 0, "XTRA" 468, "YOPQ" 79, "YTAS" 750, "YTDE" -203, "YTFI" 738, "YTLC" 514, "YTUC" 712

